import * as React from "react";
import { RadialChart, Hint } from "react-vis";
import { colors } from "../../../design-system";
import { isEmpty } from "lodash";

const totalParticipants = 1050;
const data = [
  { label: "Female", angle: 62.5 },
  { label: "Male", angle: 37.4 },
  { label: "Other", angle: 0.1 },
];

interface Props {}
const Income: React.FC<Props> = () => {
  const [value, setValue] = React.useState({});

  return (
    <div>
      <p>Income Metrics</p>
      <RadialChart
        colorRange={[colors.greyDark, colors.blueDark, colors.blue]}
        animation
        data={data}
        width={300}
        height={300}
        showLabels
        onValueMouseOver={v => {
          setValue({
            label: data.find(item => item.label === v.label).label,
            total:
              totalParticipants *
              (data.find(item => item.label === v.label).angle * 0.01),
            percentage: data.find(item => item.label === v.label).angle,
          });
        }}
        onSeriesMouseOut={v => setValue({})}
      >
        {!isEmpty(value) && <Hint value={value} />}
      </RadialChart>
    </div>
  );
};

export default Income;
